angular.module('station-ui')
	.factory('HomeService', ['HomeApi', function (homeApi) {

    return {

        getAll: function (callBack) {
            homeApi.getAll() .then(function (response) {
                callBack(response.data);
            }).catch(function (error) {
            });
        },

        getAllRecette: function (callBack) {
            homeApi.getAllRecette() .then(function (response) {
                callBack(response.data);
            }).catch(function (error) {
            });
        },
        
        getOne: function (id, callBack) {

            homeApi.getOne(id).success(function (response) {
                //custom, filter or make a specific mapping between response and UI
                var user = response && response != "" ? angular.fromJson(response) : null;
                console.log(user);

                //Delayed precessing when http request is completed
                callBack(user);
            })
                .error(function (response, status) {
                    console.log('Error with response: ' + response + ', status :' + status);
                });
        },

        add: function (entry, callBack) {
            // homeApi.addOne(entry).success(function (response) {
            //     callBack(response);
            // })
            //     .error(function (status) {
            //    alert('Error with response status :' + status);
            //     });

            homeApi.addOne(entry)
                .then(function (response) {
                    callBack(response);
                }).catch(function (error) {
                    console.debug('>> catch');
                    console.debug(error);
                });
        },

        save: function (entry,callBack) {
            homeApi.saveOne(entry)
                    .then(function (response) {
                        callBack(response);
                    }).catch(function (error) {
                        console.debug(error);
                    });
        },

        connecter: function (entry,callBack) {
            homeApi.connecter(entry)
                .then(function (response) {
                    callBack(response);
                }).catch(function (error) {
                console.debug(error);
            });
        },

        delete: function (entryId, callBack) {
            homeApi.deleteOne(entryId).success(function (response) {
                var filteredResult = response && response != "" ? angular.fromJson(response) : null;
                //console.log(filteredResult);

                //Delayed precessing when http request is completed
                callBack(filteredResult);
            })
                .error(function (response, status) {
                    console.log('Error with response: ' + response + ', status :' + status);
                });
        }

    }
}]);

(function () {
    'use strict';

    angular.module('station-ui')
        .controller('HomeController', ['$scope', 'HomeService','CuveService', HomeController]);

    /** @ngInject */
    function HomeController($scope, homeService,cuveService) {
        $scope.pompeForm = {
        };
        $scope.added=false;
        $scope.IsVisibleConnection=false;
        $scope.selectedPompe={
        };

        $scope.selectedCuve={
        };

        homeService.getAll(function (pompes) {
            $scope.pompes = pompes;
            $scope.IsVisible = true;
        });
        $scope.calculeClose=function()
        {
            $scope.pompesRecette =null;
        }
        $scope.loadAll=function()
        {
            homeService.getAll(function (pompes) {
                $scope.pompes = pompes;
            });
        }
        $scope.submitPompe = function() {
            homeService.add($scope.pompeForm, function (response) {
                $scope.added=true;
            });
        }
        $scope.updateIndex = function() {
            homeService.save($scope.selectedPompe, function (response) {
                alert('Index pompe '+$scope.selectedPompe.id +' modifié')
                $scope.selectedPompe={
                };
                $scope.loadAll();

            });
        }
        $scope.calculeResset = function() {
            homeService.getAllRecette(function (response) {
                $scope.pompesRecette = response;
            });
        }
        $scope.getTotal = function(){
            var total = 0;
            for(var i = 0; i < $scope.pompesRecette.length; i++){
                var pompe = $scope.pompesRecette[i];
                total += pompe.recette;
            }
            return total;
        }

        $scope.getAllCuves = function() {
            cuveService.getAll(function (cuves) {
                $scope.cuves = cuves;
                if (cuves) {
                    $scope.IsVisibleConnection=true;
                }
            });
        }

        $scope.connecter = function() {
           var request ={"idPompe":$scope.selectedPompe.id,
                          "idCuve":$scope.selectedCuve.id
           };
            homeService.connecter(request, function (response) {
                alert(' pompe '+$scope.selectedPompe.id +' connecter à la cuve '+ $scope.selectedCuve.id);
                $scope.selectedPompe={

                }
                $scope.selectedCuve={

                }
            });
        }

    }
})();

'use strict';

angular.module('station-ui')
    .factory('HomeApi', ['$http', 'BASE_URL', homeApi]);

function homeApi($http, BASE_URL) {

    var BASE_URL_SELF = BASE_URL + '/rest/pompe';

    return {

        getAll: function () {
            return $http.get(BASE_URL_SELF+'/all');
        },
        getAllRecette : function () {
            return $http.get(BASE_URL_SELF+'/calculate');
        },

        getOne: function (id) {
            return $http.get(BASE_URL_SELF + id + '/');
        },

        addOne: function (pompe) {
            //console.debug($http.post(BASE_URL_SELF, pompe));
            return $http.post(BASE_URL_SELF, pompe);
        },
        connecter: function (request) {
            //console.debug($http.post(BASE_URL_SELF, pompe));
            return $http.put(BASE_URL_SELF + "/connection" + '/', request);
        },

        saveOne: function (pompe) {
            return $http.put(BASE_URL_SELF + "/updateIndex" + '/', pompe);
        },

        deleteOne: function (id) {
            return $http.delete(BASE_URL_SELF + id + '/');
        }
    }
}

