(function () {
    'use strict';

    angular.module('station-ui')
        .controller('CuveController', ['$scope', '$state', '$stateParams', 'CuveService', CuveController]);

    /** @ngInject */
    function CuveController($scope, $state, $stateParams, cuveService) {

        $scope.cuveForm = {
        };
        $scope.added=false;
        cuveService.getAll(function (cuves) {
            $scope.isVisiblesVisible = false;
            $scope.cuves = cuves;
            if(cuves) {
                $scope.isVisible = true;
            }
        });

        $scope.submitCuve = function() {
            cuveService.add($scope.cuveForm, function (response) {
                //$stateProvider.go('home');
                //$state.go('cuve-edit', {idCuve:'10'});
                $scope.added=true;
            });
        }
    }
})();

'use strict';

angular.module('station-ui')
    .factory('CuveApi', ['$http', 'BASE_URL', cuveApi]);

function cuveApi($http, BASE_URL) {

    var BASE_URL_SELF = BASE_URL + '/rest/cuve';

    return {

        getAll: function () {
            return $http.get(BASE_URL_SELF+'/all');
        },

        getOne: function (id) {
            return $http.get(BASE_URL_SELF + id + '/');
        },

        addOne: function (cuve) {
            //console.debug($http.post(BASE_URL_SELF, pompe));
            return $http.post(BASE_URL_SELF, cuve);
        },

        saveOne: function (brand) {
            return $http.put(BASE_URL_SELF + brand.id + '/', brand);
        },

        deleteOne: function (id) {
            return $http.delete(BASE_URL_SELF + id + '/');
        }
    }
}

